.todo-list {
    display: flex;
    flex-direction: column;    
    list-style: none;
    font-size: $base-font;
}

.todo-list__item {
    padding: 10px;
    margin-bottom: 15px;
    font-size: 18px;
    font-weight: bold;
    background-color: white;
    color: $black;
    border-radius: 10px;
}